import _ from 'lodash'
import ACTION from '../actionTypes/homepageActionTypes'
import { urlToKey, getSheet } from 'sheetsy'
import mapSheetResToObject from '../../utils/mapSheetResToObject'
import { SHEET_KEY } from '../../commons/constant'

const getContent = () => async dispatch => {
  const pageSheet = await getSheet(urlToKey(SHEET_KEY), 'od6')
  const sponsorSheet = await getSheet(urlToKey(SHEET_KEY), 'of26me')
  const pageItems = mapSheetResToObject(pageSheet)
  const sponsorItems = _.reduce(
    sponsorSheet.rows,
    (merged, item) => [
      ...merged,
      {
        name: item.name,
        url: item.url,
      },
    ],
    []
  )
  dispatch(setContent({ ...pageItems, sponsor: sponsorItems }))
}

const setContent = payload => ({
  type: ACTION.SET_HOMEPAGE_CONTENT,
  payload,
})

export default {
  getContent,
  setContent,
}
