import initialState from './initialState'
import ACTION from '../../actionTypes/headerActionTypes'

const headerReducers = (state = initialState, action) => {
  switch (action.type) {
    case ACTION.SET_HEADER_IMAGE:
      return {
        ...state,
        logoDesktop: action.payload.desktop,
        logoMobile: action.payload.mobile,
      }
    default:
      return state
  }
}

export default headerReducers
