import ACTION from '../actionTypes/headerActionTypes'
import { urlToKey, getSheet } from 'sheetsy'
import mapSheetResToObject from '../../utils/mapSheetResToObject'
import { SHEET_KEY } from '../../commons/constant'

const getImage = () => async dispatch => {
  const sheet = await getSheet(urlToKey(SHEET_KEY), 'o4kn891')
  const sheetItems = mapSheetResToObject(sheet)
  dispatch(setImage({ mobile: sheetItems.logoMobile, desktop: sheetItems.logoDesktop }))
}

const setImage = ({ mobile, desktop }) => ({
  type: ACTION.SET_HEADER_IMAGE,
  payload: {
    mobile,
    desktop,
  },
})

export default {
  getImage,
  setImage,
}
