import { Responsive } from 'semantic-ui-react'
import React, { Component, Fragment } from 'react'
import { withStyles } from '@material-ui/core/styles'
import {
  AppBar,
  Drawer,
  Toolbar,
  Button,
  IconButton,
  List,
  ListItem,
  ListItemText,
  Divider,
} from '@material-ui/core'
import MenuIcon from '@material-ui/icons/Menu'
import Icon from '@material-ui/core/Icon'
import PropTypes from 'prop-types'
import styles from './styles'

class HeaderMobile extends Component {
  state = {
    showMenu: false,
  }

  static propTypes = {
    classes: PropTypes.object.isRequired,
    menu: PropTypes.array,
    onClickRegister: PropTypes.func,
    changeColor: PropTypes.bool,
    logo: PropTypes.string,
  }

  static defaultProps = {
    menu: [],
    onClickRegister: () => {},
    changeColor: false,
    logo: '',
  }

  toggleMenu = visible => () => this.setState({ showMenu: visible })

  render() {
    const { classes, menu, onClickRegister, changeColor, logo } = this.props
    const { showMenu } = this.state
    return (
      <Responsive as={Fragment} {...Responsive.onlyMobile}>
        <AppBar position="fixed" className={changeColor ? classes.appBarAlt : classes.appBar}>
          <Toolbar>
            <div className={`${classes.sideContainerMobile} ${classes.imgContainer}`}>
              {logo && <img className={classes.logoImgMobile} src={logo} alt="logo mobile" />}
            </div>
            <div className={classes.middleContainer}>
              <Button
                variant={changeColor ? 'contained' : 'outlined'}
                color="secondary"
                className={classes.button}
                onClick={onClickRegister}
              >
                เป็นส่วนหนึ่งกับเรา
              </Button>
            </div>
            <div className={`${classes.sideContainerMobile} ${classes.menuContainer}`}>
              <IconButton
                edge="start"
                className={classes.menuButton}
                onClick={this.toggleMenu(true)}
                color="inherit"
                aria-label="menu"
              >
                <MenuIcon />
              </IconButton>
            </div>
          </Toolbar>
        </AppBar>
        <Drawer anchor="right" open={showMenu} onClose={this.toggleMenu(false)}>
          <div
            className={classes.list}
            role="presentation"
            onClick={this.toggleMenu(false)}
            onKeyDown={this.toggleMenu(false)}
          >
            <List>
              {menu.length &&
                menu.map(item => (
                  <ListItem key={item.title} onClick={item.onClick} button>
                    <ListItemText primary={item.title} />
                  </ListItem>
                ))}
              <ListItem button>
                <Button
                  variant="contained"
                  color="secondary"
                  className={classes.button}
                  onClick={onClickRegister}
                >
                  <ListItemText primary="เป็นส่วนหนึ่งกับเรา" />
                </Button>
              </ListItem>
            </List>
            <Divider />
            <div className={classes.socialContainer}>
              <Icon className="fab fa-facebook-square" fontSize="large" />
              <Icon className="fab fa-instagram" fontSize="large" />
              <Icon className="fab fa-youtube-square" fontSize="large" />
            </div>
          </div>
        </Drawer>
      </Responsive>
    )
  }
}

export default withStyles(styles)(HeaderMobile)
