import React, { Component, Fragment } from 'react'
import { Responsive } from 'semantic-ui-react'
import { Toolbar, Button, AppBar } from '@material-ui/core'
import { withStyles } from '@material-ui/core/styles'
import PropTypes from 'prop-types'
import styles from './styles'

class HeaderDesktop extends Component {
  static propTypes = {
    classes: PropTypes.object.isRequired,
    menu: PropTypes.array,
    onClickRegister: PropTypes.func,
    changeColor: PropTypes.bool,
    logo: PropTypes.string,
  }

  static defaultProps = {
    menu: [],
    onClickRegister: () => {},
    changeColor: false,
    logo: '',
  }

  render() {
    const { classes, menu, onClickRegister, changeColor, logo } = this.props

    return (
      <Responsive as={Fragment} minWidth={Responsive.onlyTablet.minWidth}>
        <AppBar position="fixed" className={changeColor ? classes.appBarAlt : classes.appBar}>
          <Toolbar>
            <div className={classes.imgContainer}>
              {logo && <img className={classes.logoImgDesktop} src={logo} alt="logoDesktop" />}
            </div>
            <div className={classes.middleContainer} />
            {!!menu.length &&
              menu.map(item => (
                <Button key={item.title} onClick={item.onClick}>
                  {item.title}
                </Button>
              ))}
            <Button
              variant={changeColor ? 'contained' : 'outlined'}
              color="secondary"
              className={classes.button}
              onClick={onClickRegister}
            >
              เป็นส่วนหนึ่งกับเรา
            </Button>
          </Toolbar>
        </AppBar>
      </Responsive>
    )
  }
}

export default withStyles(styles)(HeaderDesktop)
