import React, { Fragment } from 'react'
import { Button, Container, Typography, Icon } from '@material-ui/core'
import _ from 'lodash'
import { withStyles } from '@material-ui/core/styles'
import PropTypes from 'prop-types'
import { Parallax, Background } from 'react-parallax'
import { Responsive } from 'semantic-ui-react'
import { connect } from 'react-redux'
import RegisterContainer from '../../containers/Register'
import ScrollDownIcon from '../../components/ScrollDownIcon'
import styles from './styles'
import homepageSelector from '../../redux/selector/homepageSelector'
import ACTION from '../../redux/actions/homepageAction'

const ME =
  'https://scontent.fbkk2-8.fna.fbcdn.net/v/t1.0-9/66121815_2330915116969006_5671729606877511680_n.jpg?_nc_cat=103&_nc_eui2=AeE2to6M5njuuumS8b2Z1YmMt5jsy3icALQQ52gOmzyWZxaaAT6egErjxRjRBG-yNGpcN5wa5ZoI1oY5vzdny-ANhwJE_nAsP3vMhYBi3mj__w&_nc_oc=AQkdCR1RWxfcJABqWg7nqG-I8CqLHk_IWI16w839qMhZbhb82iSgq9GHLhlCd41_H1s&_nc_ht=scontent.fbkk2-8.fna&oh=efa1c543f5753d73fa96971177910d7a&oe=5DDB4DEE'
class Index extends React.Component {
  static propTypes = {
    classes: PropTypes.object.isRequired,
    bgHeader: PropTypes.string.isRequired,
    sponsor: PropTypes.array.isRequired,
    getContent: PropTypes.func.isRequired,
  }

  componentDidMount() {
    this.props.getContent()
  }

  setShowButton = show => {
    this.setState({ showButton: show })
  }

  setShowMessage = show => {
    this.setState({ showMessage: show })
  }

  state = {
    isShowRegister: false,
  }

  handleOpenRegister = () => this.setState({ isShowRegister: true })

  handleCloseRegister = () => this.setState({ isShowRegister: false })

  BannerButton = ({ className, children, variant, color, onClick }) => {
    const { classes } = this.props
    return (
      <Button
        color={color || 'default'}
        variant={variant || 'contained'}
        aria-label="delete"
        className={classes.bannerButton}
        onClick={onClick}
      >
        {className && <Icon className={className} fontSize="default" />}
        <div className={classes.bannerButtonTextContainer}>{children}</div>
      </Button>
    )
  }

  RenderLogo = ({ percentage }) => {
    // console.log('percentage', percentage)
    const { sponsor, classes } = this.props
    return (
      <div className={classes.sponsorWrapper}>
        <div className={classes.sponsorContainer}>
          <Responsive {...Responsive.onlyMobile}>
            {sponsor.map((item, index) => (
              <img
                src={item.url}
                alt={item.name}
                key={item.name}
                className={classes.sponsorImageMobile}
                style={{
                  opacity: _.max([0.1, percentage * 6 - index * 0.2]),
                  transform: `translateY(${_.max([0, 100 - percentage * 1000 + index * 15])}px)`,
                }}
              />
            ))}
          </Responsive>
          <Responsive minWidth={Responsive.onlyTablet.minWidth}>
            {sponsor.map((item, index) => (
              <img
                src={item.url}
                alt={item.name}
                key={item.name}
                className={classes.sponsorImageDesktop}
                style={{
                  opacity: _.max([0.1, percentage * 8 - index * 0.2]),
                  transform: `translateY(${_.max([0, 100 - percentage * 1500 + index * 50])}px)`,
                }}
              />
            ))}
          </Responsive>
        </div>
      </div>
    )
  }

  render() {
    const { classes, bgHeader } = this.props
    const { isShowRegister } = this.state
    const { BannerButton, RenderLogo } = this
    return (
      <>
        <RegisterContainer visible={isShowRegister} handleClose={this.handleCloseRegister} />
        <div className={classes.pageWrapper}>
          <Parallax blur={{ min: -2, max: 4 }} bgImage={bgHeader} strength={100}>
            <div className={classes.bannerColorOverlay}>
              <div className={classes.bannerContainer}>
                <Container className={classes.bannerTextContainer}>
                  <Responsive as={Fragment} {...Responsive.onlyMobile}>
                    <Typography align="center" variant="h4" color="textPrimary" paragraph>
                      มาร่วมเป็นส่วนหนึ่งกับเรา <br /> CAMT CHEERLEADER
                    </Typography>
                  </Responsive>
                  <Responsive as={Fragment} minWidth={Responsive.onlyTablet.minWidth}>
                    <Typography align="center" variant="h3" color="textPrimary" paragraph>
                      มาร่วมเป็นส่วนหนึ่งกับเรา <br /> CAMT CHEERLEADER
                    </Typography>
                  </Responsive>
                  <div className={classes.bannerButtonContainer}>
                    <BannerButton variant="outlined" className="fas fa-play">
                      ดูวิดีโอ
                    </BannerButton>
                    <BannerButton color="secondary" onClick={this.handleOpenRegister}>
                      สมัครสมาชิก
                    </BannerButton>
                  </div>
                </Container>
                <div className={classes.scrollDownContainer}>
                  <ScrollDownIcon />
                </div>
              </div>
            </div>
          </Parallax>
          <div style={{ height: 125 }} />
          <Typography variant="h4" align="center" paragraph>
            สนับสนุนโดย
          </Typography>
          <Parallax renderLayer={percentage => <RenderLogo percentage={percentage} />}>
            <Background />
          </Parallax>
          <div style={{ height: '100vh' }} />
          <img src={ME} style={{ width: '100%' }} alt="ME ไง" />
        </div>
      </>
    )
  }
}

const mapStateToProps = state => {
  return homepageSelector(state)
}

const mapDispatchToProps = {
  getContent: ACTION.getContent,
}

const styleConnector = withStyles(styles)(Index)

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(styleConnector)
