import React, { Component, Fragment } from 'react'
import {
  Backdrop,
  Fade,
  Modal,
  Container,
  Icon,
  FormControl,
  InputLabel,
  Input,
  FormHelperText,
  TextField,
  MenuItem,
  Button,
  Typography,
  RadioGroup,
  FormControlLabel,
  Radio,
} from '@material-ui/core'
import ChevronRight from '@material-ui/icons/ChevronRight'
import _ from 'lodash'
import { withStyles } from '@material-ui/core/styles'
import GoogleForm from 'google-form-send'
import PropTypes from 'prop-types'
import GradientStepper from '../../components/Stepper/GradientStepper'
import styles from './styles'

const FORM_URL = 'https://docs.google.com/forms/d/1iZTU2LBaROdX-bjphS7eXnHsAzlyULVa94_aLqPYnME'

const STEPS = [
  {
    label: 'ข้อมูล นศ.',
    icon: (
      <Icon
        className="fas fa-user-graduate"
        style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}
      />
    ),
  },
  {
    label: 'ข้อมูลส่วนตัว',
    icon: (
      <Icon
        className="fas fa-user"
        style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}
      />
    ),
  },
  {
    label: 'ข้อมูลการติดต่อ',
    icon: (
      <Icon
        className="fas fa-phone-volume"
        style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}
      />
    ),
  },
]

const MAJOR = [
  { label: 'วิศวะกรรมซอฟต์แวร์', value: 'SE' },
  { label: 'การจัดการสมัยใหม่และเทคโนโลยีสารสนเทศ', value: 'MMIT' },
  { label: 'แอนิเมชั่นและวิชวลเอฟเฟกต์', value: 'ANI' },
  { label: 'ดิจิทัลเกม', value: 'DG' },
  { label: 'บูรณาการอุตสาหกรรมดิจิทัล', value: 'DII' },
]

const defaultForm = {
  stdID: '',
  major: '',
  name: '',
  nickName: '',
  gender: '',
  phone: '',
  facebook: '',
  line: '',
}

export class RegisterContainer extends Component {
  static propTypes = {
    classes: PropTypes.object.isRequired,
    visible: PropTypes.bool,
    handleClose: PropTypes.func,
  }

  static defaultProps = {
    visible: false,
    handleClose: () => {},
  }

  state = {
    currentStep: 0,
    formValues: { ...defaultForm },
    errors: { ...defaultForm },
  }

  setStep = step => this.setState({ currentStep: step - 1 })

  handleChange = name => event => {
    const { setValue } = this
    const { value } = event.target
    event.persist()
    if (name === 'stdID') {
      const regex = /^[0-9]{0,9}$/
      if (value.match(regex)) {
        setValue(name, value)
      }
    } else if (name === 'name' || name === 'nickName') {
      const nameRegex = /[!$%^&*()_+|~\-=`{}[\]:";<>?,./]/g
      const removeCharacter = _.replace(value, nameRegex, '')
      setValue(name, _.replace(removeCharacter, '  ', ' '))
    } else if (name === 'phone') {
      const regex = /^[0-9]{0,10}$/
      if (value.match(regex)) {
        setValue(name, value)
      }
    } else if (name === 'facebook' || name === 'line') {
      setValue(name, _.replace(value, '  ', ' '))
    } else {
      setValue(name, value)
    }
  }

  setValue = (name, value) =>
    this.setState(state => ({
      formValues: {
        ...state.formValues,
        [name]: value,
      },
      errors: {
        ...state.errors,
        [name]: '',
      },
    }))

  handleSubmitStep1 = () => {
    const stdIDValidate = /^[0-9]{9}$/
    const { formValues } = this.state
    const newErrors = { ...defaultForm }
    if (!formValues.stdID) {
      newErrors.stdID = 'กรุณากรอกรหัสนักศึกษา'
    } else if (!formValues.stdID.match(stdIDValidate)) {
      newErrors.stdID = 'รหัสนักศึกษาต้องมี 9 ตัวอักษร'
    }

    if (!formValues.major) {
      newErrors.major = 'กรุณาเลือกสาขา'
    }

    // Form decision
    const result = _.isEqual(newErrors, defaultForm)
    if (result) {
      this.setStep(2)
    } else {
      this.setState({ errors: newErrors })
    }
    return result
  }

  handleSubmitStep2 = () => {
    const { formValues } = this.state
    const newErrors = { ...defaultForm }
    if (!formValues.name) {
      newErrors.name = 'กรุณากรอกชื่อและนามสกุล'
    } else if (_.split(_.trim(formValues.name), ' ').length === 1) {
      newErrors.name = 'กรุณากรอกนามสกุล'
    }
    if (!formValues.nickName) {
      newErrors.nickName = 'กรุณากรอกชื่อเล่น'
    }
    if (!formValues.gender) {
      newErrors.gender = 'please select gender'
    }

    // Form decision
    const result = _.isEqual(newErrors, defaultForm)
    if (result) {
      this.setStep(3)
    } else {
      this.setState({ errors: newErrors })
    }
    return result
  }

  handleSubmitStep3 = () => {
    const { formValues } = this.state
    const newErrors = { ...defaultForm }
    const phoneValidate = /^[0-9]{10}$/
    if (!formValues.phone) {
      newErrors.phone = 'กรุณากรอกเบอร์โทรติดต่อ'
    } else if (!formValues.phone.match(phoneValidate)) {
      newErrors.phone = 'เบอร์โทรติดต่อต้องมี 10 ตัวอักษร'
    }
    if (!formValues.facebook) {
      newErrors.facebook = 'กรุณากรอก Facebook'
    }
    if (!formValues.line) {
      newErrors.line = 'กรุณากรอก Line'
    }

    // Form decision
    const result = _.isEqual(newErrors, defaultForm)
    if (!result) {
      this.setState({ errors: newErrors })
    }
    return result
  }

  handleSubmitForm = () => {
    const { handleClose } = this.props
    const { formValues } = this.state
    if (!this.handleSubmitStep3()) {
      this.setStep(3)
      return false
    }
    if (!this.handleSubmitStep1()) {
      this.setStep(1)
      return false
    }
    if (!this.handleSubmitStep2()) {
      this.setStep(2)
      return false
    }
    const splitName = _.split(_.replace(_.trim(formValues.name), /[ ]/, '&'), '&')
    const firstName = splitName[0]
    const lastName = splitName[1]
    const mapValue = {
      'entry.1876696469': formValues.stdID,
      'entry.1151401616': formValues.major,
      'entry.778059248': firstName,
      'entry.813027883': lastName,
      'entry.353031777': _.trim(formValues.nickName),
      'entry.1505962253': _.trim(formValues.gender),
      'entry.760563390': formValues.phone,
      'entry.49246998': formValues.facebook,
      'entry.1423887688': formValues.line,
    }
    const form = new GoogleForm(FORM_URL)
    form.setAllFields(mapValue)
    form.send(true)
    handleClose()
    this.setState({ formValues: defaultForm, currentStep: 0 })
  }

  ButtonStep = ({ onClick = () => {}, title = '', showIcon = false, type }) => {
    const { classes } = this.props
    return (
      <div className={classes.stepButtonContainer}>
        <Button
          variant="contained"
          color="secondary"
          onClick={onClick}
          className={classes.stepButton}
          type={type}
        >
          <Typography align="center" className={classes.stepButtonText}>
            {title}
          </Typography>
          {showIcon && <ChevronRight />}
        </Button>
      </div>
    )
  }

  RenderTextInput = ({ label, value }) => {
    const { classes } = this.props
    const { formValues, errors } = this.state
    return (
      <FormControl className={classes.inputStyle} error={!!errors[value]}>
        <InputLabel htmlFor={value}>{label}</InputLabel>
        <Input
          id={value}
          value={formValues[value]}
          onChange={this.handleChange(value)}
          aria-describedby={`${value}-text`}
        />
        <FormHelperText id={`${value}-text`}>{errors[value] || ' '}</FormHelperText>
      </FormControl>
    )
  }

  renderFormStep = () => {
    const { classes } = this.props
    const { formValues, errors, currentStep } = this.state
    const { ButtonStep, RenderTextInput } = this
    if (currentStep === 0) {
      return (
        <Fragment>
          <div className={classes.inputContainer}>
            <RenderTextInput label="รหัสนักศึกษา" value="stdID" />
            <TextField
              id="major"
              select
              label="สาขา"
              className={classes.inputStyle}
              value={formValues.major}
              onChange={this.handleChange('major')}
              error={!!errors.major}
              helperText={errors.major || ' '}
            >
              {MAJOR.map(option => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </TextField>
          </div>
          <ButtonStep title="ขั้นตอนต่อไป" onClick={this.handleSubmitStep1} showIcon />
        </Fragment>
      )
    }
    if (currentStep === 1) {
      return (
        <Fragment>
          <div className={classes.inputContainer}>
            <RenderTextInput label="ชื่อ-สกุล" value="name" />
            <RenderTextInput label="ชื่อเล่น" value="nickName" />
            <RadioGroup
              aria-label="gender"
              name="gender"
              className={classes.genderSelector}
              value={formValues.gender}
              onChange={this.handleChange('gender')}
            >
              <FormControlLabel
                value="female"
                control={<Radio className={errors.gender && classes.genderError} />}
                label={
                  <Typography color={errors.gender ? 'error' : 'textPrimary'}>ผู้หญิง</Typography>
                }
              />
              <FormControlLabel
                value="male"
                control={<Radio className={errors.gender && classes.genderError} />}
                label={
                  <Typography color={errors.gender ? 'error' : 'textPrimary'}>ผู้ชาย</Typography>
                }
              />
            </RadioGroup>
          </div>
          <ButtonStep title="ขั้นตอนสุดท้าย" onClick={this.handleSubmitStep2} showIcon />
        </Fragment>
      )
    }
    if (currentStep === 2) {
      return (
        <Fragment>
          <div className={classes.inputContainer}>
            <RenderTextInput label="เบอร์โทรติดต่อ" value="phone" />
            <RenderTextInput label="Facebook" value="facebook" />
            <RenderTextInput label="Line" value="line" />
          </div>
          <ButtonStep title="เสร็จสิ้น" onClick={this.handleSubmitForm} />
        </Fragment>
      )
    }
    return <></>
  }

  render() {
    const { visible, classes, handleClose } = this.props
    const { currentStep } = this.state

    return (
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={visible}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={visible}>
          <Container className={classes.paper}>
            <GradientStepper activeStep={currentStep} steps={STEPS} />
            <div className={classes.formStyle}>{this.renderFormStep()}</div>
          </Container>
        </Fade>
      </Modal>
    )
  }
}

export default withStyles(styles)(RegisterContainer)
