import React, { Component } from 'react'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'
import RegisterContainer from '../../containers/Register'
import HeaderMobile from './HeaderMobile'
import HeaderDesktop from './HeaderDesktop'
import ACTION from '../../redux/actions/headerAction'
import headerSelector from '../../redux/selector/headerSelector'

const MENU_LIST = [
  {
    title: 'หน้าแรก',
    onClick: () => console.log('หน้าแรก'),
  },
  {
    title: 'เกี่ยวกับเรา',
    onClick: () => console.log('เกี่ยวกับเรา'),
  },
  {
    title: 'ทำเนียบรุ่น',
    onClick: () => console.log('ทำเนียบรุ่น'),
  },
  {
    title: 'ติดต่อเรา',
    onClick: () => console.log('ติดต่อเรา'),
  },
]

const APPBAR_CHANGE_HEIGHT = 50

class Header extends Component {
  state = {
    showRegister: false,
    appBarChangeColor: false,
  }

  static propTypes = {
    getImage: PropTypes.func.isRequired,
    logoDesktop: PropTypes.string.isRequired,
    logoMobile: PropTypes.string.isRequired,
  }

  componentDidMount() {
    const { logoDesktop, logoMobile, getImage } = this.props
    if (!logoDesktop || !logoMobile) {
      getImage()
    }
    window.addEventListener('scroll', this.handleScroll)
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll)
  }

  handleScroll = () => {
    const { appBarChangeColor } = this.state
    if (window.scrollY >= APPBAR_CHANGE_HEIGHT && !appBarChangeColor) {
      this.setState({ appBarChangeColor: true })
    }
    if (window.scrollY < APPBAR_CHANGE_HEIGHT && appBarChangeColor) {
      this.setState({ appBarChangeColor: false })
    }
  }

  onOpenRegister = () => this.setState({ showRegister: true })

  onCloseRegister = () => this.setState({ showRegister: false })

  handleClickRegister = () => this.onOpenRegister()

  render() {
    const { showRegister, appBarChangeColor } = this.state
    const { logoDesktop, logoMobile } = this.props
    return (
      <>
        <HeaderDesktop
          changeColor={appBarChangeColor}
          onClickRegister={this.handleClickRegister}
          logo={logoDesktop}
          menu={MENU_LIST}
        />
        <HeaderMobile
          changeColor={appBarChangeColor}
          onClickRegister={this.handleClickRegister}
          logo={logoMobile}
          menu={MENU_LIST}
        />
        <RegisterContainer visible={showRegister} handleClose={this.onCloseRegister} />
      </>
    )
  }
}

const mapStateToProps = state => {
  return headerSelector(state)
}

const mapDispatchToProps = {
  getImage: ACTION.getImage,
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Header)
