import initialState from './initialState'
import ACTION from '../../actionTypes/homepageActionTypes'

const homepageReducer = (state = initialState, action) => {
  switch (action.type) {
    case ACTION.SET_HOMEPAGE_CONTENT:
      return {
        ...state,
        ...action.payload,
      }
    default:
      return state
  }
}

export default homepageReducer
